.App {
  text-align: center;
}

.page {
  background-color: #333;
  padding: 20px;
  border-radius: 10px;
  margin: 3% 20.2%;
}


@media (max-width: 1150px) {
  .page {
    margin: 3%;
  }
}

h1 {
  margin-top: 10px;
  color: #ddd;
}

h2 {
  margin-bottom: 10px;
}

.dropzone {
  border: 2px dashed #fff;
  padding: 20px;
  cursor: pointer;
}

.dropzone p {
  margin: 0;
}

button {
  margin: 15px 10px 0px;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

img {
  max-width: 100%;
  border-radius: 5px;
}

.image-wrapper {
  display: flex;
  justify-content: center; /* 水平置中 */
  align-items: center; /* 垂直置中 */
  margin-top: 0px;
  margin-bottom: 10px;
}

.image-container {
  margin: 30px;
}

@media screen and (max-width: 670px) {
  .hide-on-screens {
    display: none;
  }
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 15px;
  height: 100%;
}

.loading-spinner {
  border: 4px solid rgba(0, 0, 0, 0.3);
  border-top: 4px solid #3498db;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}